import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Entry, Logbook } from '../types';
import {DataSource} from '@angular/cdk';
import * as _ from 'underscore';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
})
export class MainComponent implements OnInit {
  data:Logbook[] = [];
  pageData:Logbook[] = [];
  perPage = 5;
  pageCount = 1;
  loading = true;
  currentPage:number;
  constructor(private dataService:DataService) { }
  displayedColumns = ["Id", "Title"];
  ngOnInit() {
    this.dataService.getAllLogbooks().then(res => {
      this.data = res;
      this.loading = false;
      this.pageCount = Math.ceil(res.length / this.perPage);
      this.currentPage = 0;
      this.updatePage();
    })
  }
  pages(){
    return new Array(this.pageCount);
  }
  updatePage(){
    let start = this.currentPage * this.perPage;
    let end = start + this.perPage;
    this.pageData = this.data.slice(start, end);
  }
  selectPage(index:number){
    this.currentPage = index;
    this.updatePage();
  }
  prevPage(){
    if(this.currentPage > 0){
      this.currentPage--;
      this.updatePage();
    }
    return false;
  }
  nextPage(){
    if(this.currentPage + 1 < this.pageCount){
      this.currentPage++;
      this.updatePage();
    }
    return false;
  }
}



