import { BrowserModule } from '@angular/platform-browser';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import { RouterModule, Routes } from '@angular/router';
import { NgModule } from '@angular/core';
import { FormsModule } from "@angular/forms";
import {HttpModule } from "@angular/http";
import {MdButtonModule, MdCheckboxModule, MdInputModule, MdTableModule, MdDatepickerModule, MdSelectModule, MdNativeDateModule} from '@angular/material';
import { DataService } from './data.service';

import { AppComponent } from './app.component';
import { MainComponent } from './main/main.component';
import { EditComponent } from './edit/edit.component';

const routes:Routes = [
  { path: '', component: MainComponent },
  { path: 'edit/:id', component: EditComponent },
  { path: 'create', component: EditComponent },
  { path: '**', component: MainComponent }
]

@NgModule({
  declarations: [
    AppComponent,
    MainComponent,
    EditComponent
  ],
  imports: [
    RouterModule.forRoot(routes, { useHash: true }),
    FormsModule,
    BrowserModule,
    HttpModule,
    BrowserAnimationsModule,
    MdButtonModule, MdCheckboxModule, MdInputModule, MdTableModule, MdDatepickerModule, MdSelectModule, MdNativeDateModule
  ],
  providers: [DataService],
  bootstrap: [AppComponent]
})
export class AppModule { }



