import { Injectable } from '@angular/core';
import {Http ,Headers } from "@angular/http";
import { Entry, Logbook, Aircraft, Pilot } from './types';
import 'rxjs';

@Injectable()
export class DataService {
  private baseUrl = "/api/";

  aircrafts:Aircraft[] = [];
  pilots:Pilot[] = [];

  constructor(private http:Http) { }

  getAllLogbooks():Promise<Logbook[]>{
    return this.http.get(this.baseUrl + "logbook/getall")
    .toPromise()
    .then(json => {
      let results:Logbook[] = json.json();
      for(let i = 0; i < results.length; i++){
        let res = results[i];
        res.Departure = new Date((res.Departure as any).replace("Z", ""));
        res.Landing = new Date((res.Landing as any).replace("Z", ""));
        return results;
      }
    })
    .catch(err => err.json());
  }
  getSingleLogbook(id:number):Promise<Logbook>{
    return this.http.get(this.baseUrl + "logbook/get/" + id)
    .toPromise()
    .then(json => {
      let res:Logbook = json.json();
      res.Departure = new Date((res.Departure as any).replace("Z", ""));
      res.Landing = new Date((res.Landing as any).replace("Z", ""));
      return res;
    })
    .catch(err => err.json());
  }

  getPilots():Promise<Pilot[]>{
    return new Promise<Pilot[]>((res, rej) => {
      if(this.pilots.length == 0){
        this.http.get(this.baseUrl + "logbook/pilots")
        .toPromise()
        .then(result => {
          this.pilots = result.json();
          res(this.pilots);
        })
        .catch(err => {
          rej(err);
        })
      } else {
        res(this.pilots);
      }
    })
  }

  getAircrafts():Promise<Aircraft[]>{
    return new Promise<Aircraft[]>((res, rej) => {
      if(this.pilots.length == 0){
        this.http.get(this.baseUrl + "logbook/aircrafts")
        .toPromise()
        .then(result => {
          this.aircrafts = result.json();
          res(this.aircrafts);
        })
        .catch(err => {
          rej(err);
        })
      } else {
        res(this.aircrafts);
      }
    })
  }

  saveLogbook(payload:Logbook):Promise<any>{
    let header:Headers = new Headers({
      "content-type": "application/json"
    });
    return this.http.post(this.baseUrl + "logbook/update/" + payload.Id, payload, { headers: header})
    .toPromise();
  }

    createLogbook(payload:Logbook):Promise<any>{
    let header:Headers = new Headers({
      "content-type": "application/json"
    });
    return this.http.post(this.baseUrl + "logbook/create", payload, { headers: header})
    .toPromise();
  }
}



